<template>
  <section :class="{recommendContainer: 1, recommendLogin: recommendLogin}">
    <transition enter-active-class="zoomIn" leave-active-class="zoomOut">
      <login v-if="!LOGIN_USER_OBJECT && showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>
    </transition>

    <work-area-item v-if="WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST.length > 0" :areaList="WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST"></work-area-item>
    
    <div v-if="!WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST">
      <img src="../../assets/img/jeph52zp.png">
    </div>
    <div style="padding: 10px 0 60px 0;text-align: center;background-color: #fff;color: #777;" v-if="falgNoData">没有更多了~</div>
  </section>
</template>
<script>
  import login from '@/components/login'
  import workAreaItem from '@/components/workAreaItem'
  import Vue from 'vue'
  import VueLazyload from 'vue-lazyload'
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('workRangeModule')
  
  Vue.use(VueLazyload, {
    error: require('../../assets/img/default_blue.png'),
    loading: require('../../assets/img/Loading4.gif')
  })
  
  export default {
    components: {
      login,
      workAreaItem
    },
    data() {
      return {
        // 未点赞
        circleFollow: require('../../assets/img/zuoyexiangqing_dianzan.png'),
        // 点赞
        allFollow: require('../../assets/img/shouye_dainzan_select.png'),
        // 语音
        falgNoData: false,
        pagination: true,
        showData: false,
        showLoginModal: 0,
        recommendLogin: 0,
        lazyLoadImg: require('../../assets/img/Loading4.gif')
      }
    },
    methods: {
      ...mapActions([
        'WORK_RANGE_HEAT'
      ]),
      // 子级组件自定义事件
      showDialogEvent(data) {
        this.showLoginModal = data
      },
      // 进入作业任务详情
      __enterWorkClaim(item) {
        this.$router.push({
          name: 'workRe',
          params: {
            taskId: item.jobcompletionE.taskId,
            title: item.jobcompletionE.title
          }
        });
      },
      // 手势上滑加载数据
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          return
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行

          this.loading()

          this.falgNoData = true
          this.WORK_RANGE_HEAT({
            page: ++this.$store.state.workRangeModule.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_PAGE,
            categorieId: this.$route.params.id
            }).then( res => {
              this.hideLoading()
            if (this.pagination) {
              if (res.data.list.length > 0) {
                this.falgNoData = false
              } else {
                this.$Toast({
                  message: '没有更多了',
                  position: 'middle',
                  duration: 1000
                })
                this.pagination = false
                this.showData = true
                this.falgNoData = true
              }
            }
          }).catch( err => {
            console.log(err)
          })
        }
      },
    },
    mounted () {
      if(this.workRangeSavePosition){
         setTimeout(() => {
          window.scrollTo(0, this.workRangeSavePosition)
        }, 100)
      }
      
      window.onscroll = () => {
        this.dropDown()
      }
      
    },
    destroyed () {
      window.onscroll = () => {
        return
      }
    },
    created () {

      // 页面加载的等待状态
      let params = {
        page: 1,
        categorieId: this.$route.params.id
      }

      if(this.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST && !this.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST.length){
        this.$Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        })
        this.WORK_RANGE_HEAT( params ).then( data => {
          this.$Indicator.close()
        }).catch( err => {
          console.log(err)
        })
      }
    },
    computed: {
      ...mapState([
        'WORK_RANGE_HEAT_OBJECT',
      ]),
      LOGIN_USER_OBJECT(){
        return this.$store.state.userModule.LOGIN_USER_OBJECT
      },
      workRangeSavePosition(){
        return this.$store.state.workRangeSavePosition.highestHeat
      }
    },
    watch: {
      LOGIN_USER_OBJECT(state){
        if(state){
          if(this.$store.state.workRangeModule.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST.length > 0){
            this.$store.state.workRangeModule.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_PAGE = 1
            this.$store.state.workRangeModule.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST = []
            let params = {
              page: 1,
              categorieId: this.$route.params.id
            }
            
            this.WORK_RANGE_HEAT(params).then( data => {
              this.$Indicator.close()
              window.onscroll = () => {
                this.dropDown()
              }
            }).catch( err => {
              console.log(err)
            })
          }
        }
      },
      '$route'(to, from){
        if(this.LOGIN_USER_OBJECT){
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })

          this.$store.state.workRangeModule.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_PAGE = 1
          this.$store.state.workRangeModule.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST = []
          
          let params = {
            page: 1,
            categorieId: this.$route.params.id
          }

          this.WORK_RANGE_HEAT(params).then( data => {
            this.$Indicator.close()
          }).catch( err => {
            console.log(err)
          })
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  @import url('../../assets/css/workArea');
</style>



